body{
 margin: 0;
 background-color:#056679;
 font-family: sans-serif;
}
.todo-container{
 width: 350px;
margin: auto;
margin-top: 50px;
 text-align: center;
}
.input-section{
background-color: #0c5563;
padding: 18px;
border-radius: 15px;
box-shadow: 0px 7px 1px #251f1f;

}
h1{
 margin: 0;
 margin-bottom: 15px;
 color: #0a6879;
}
input::placeholder{
 color: #08869e;
 
}

.input-section input{
 border-radius: 5px;
 border: 2px solid #106879;
 width: 100%;
 box-sizing: border-box;
 padding: 10px;
 outline: none;
 background-color:#055565 ;
 color: #08869e;
}
ul{
 text-align: left;
 padding: 0;
 
}
ul li{
 list-style: none;
 background-color: #0c5563;
 color: #2f717d;
 padding: 15px;
 border-radius: 5px;
 margin-top: 5px;
 margin-bottom: 15px;
 box-shadow: 0 7px 6px #292424;
 display: flex;
}
  ul li i{
 text-align: right;
 flex: 1;
 cursor: pointer;
}